import { Divider } from '@mui/material';
import React from 'react';

export function MoveList({ moves }) {
  return (
    <section className="move-list">
      {!moves.length ? '' : <h2>Your last {moves.length} Moves</h2>}
      <Divider></Divider>
      {moves.slice(0, 3).map((move) => (
        <div key={move.to.id + Math.random()} className="move-list-item">
          <h3>To {move.to.name} </h3>➡
          <h3>Amount: $ {move.amount} In Bitcoin</h3>➡
          <h3>Sent at: {move.sentAt} </h3>
        </div>
      ))}
    </section>
  );
}
