import { Component } from 'react';
import { BitcoinApp } from './pages/BitcoinApp';
import './styles/main.scss';
import { Redirect } from 'react-router-dom/cjs/react-router-dom.min';
import { HashRouter as Router, Route, Switch } from 'react-router-dom';
import { ContactDetails } from './pages/ContactDetailsPage';
import { StatisticPage } from './pages/StatisticPage';
import { ContactPage } from './pages/ContactPage';
import { Login } from './pages/Login';
import { ContactEdit } from './pages/ContactEdit';
import { MainHeader } from './components/MainHeader';

export class App extends Component {
  state = {
    isDark: false,
  };
  get appTheme() {
    if (this.state.isDark) return 'dark-mode';
    return 'bitcoin-app';
  }
  toggleDarkMode = () => {
    this.setState({ isDark: (this.state.isDark = !this.state.isDark) });
  };

  render() {
    return (
      <Router>
        <div className={this.appTheme}>
          <MainHeader toggleDarkMode={this.toggleDarkMode} />
          <main className="container">
            <Switch>
              <Route component={ContactEdit} path="/contact/edit/:id?" />
              <Route component={ContactDetails} path="/contact/:id" />
              <Route component={ContactPage} path="/contact" />
              <Route component={StatisticPage} path="/statistic" />
              <Route component={Login} path="/login" />
              <Route component={BitcoinApp} path="/" />
            </Switch>
          </main>
        </div>
      </Router>
    );
  }
}
