import { Component } from 'react';
import { contactService } from '../services/contactService';
import Button from '@mui/material/Button';
import KeyboardReturnIcon from '@mui/icons-material/KeyboardReturn';
import { TransferFund } from '../components/TransferFund';
import { userService } from '../services/userService';
import { MoveList } from '../components/MoveList';

export class ContactDetails extends Component {
  state = {
    contact: null,
    loggedInUser: null,
  };

  componentDidMount() {
    this.loadContact();
    this.getLoggedInUser();
  }
  componentDidUpdate(prevProps, prevState) {
    if (prevProps.match.params.id !== this.props.match.params.id) {
      this.loadContact();
    }
  }

  async loadContact() {
    const contact = await contactService.getById(this.props.match.params.id);
    this.setState({ contact });
  }
  getLoggedInUser() {
    const user = userService.getUser();
    this.setState({ loggedInUser: user });
  }

  onGoBack = () => {
    this.props.history.push('/contact');
  };
  addMove = (amount, contact) => {
    userService.addMove(contact, amount);
  };

  render() {
    const { contact, loggedInUser } = this.state;
    if (!contact) return <div>Loading..</div>;
    return (
      <div className="contact-details">
        <h1>{contact.name}</h1>
        <h3>
          <span>{contact.email}</span>
        </h3>
        <h3>
          <span>{contact.phone}</span>
        </h3>
        {/* <img src={require(`../images/${contact.src}`)} alt='' /> */}

        <img src={`https://robohash.org/${contact.id}`} alt="" />

        <TransferFund addMove={this.addMove} contact={contact} />
        <MoveList moves={loggedInUser.moves} />
        <Button
          onClick={this.onGoBack}
          color="primary"
          startIcon={<KeyboardReturnIcon />}
          variant="contained"
        >
          Back
        </Button>
      </div>
    );
  }
}
