import React from 'react';

export function UserPreview({ user, rate }) {
  return (
    <div>
      <article className="user-preview">
        <h2>Hello {user.name}</h2>
        <img src={require(`../images/user-2.png`)} alt="" />
        <h3>
          <span>Your Balance: {user.coins}$</span>
        </h3>
        <div className="btc-rate">
          <h3>In Btc : {rate}</h3>{' '}
          <img src={require(`../images/bitcoin.png`)} alt="" />
        </div>
      </article>
    </div>
  );
}
