import React from 'react'
import Button from '@mui/material/Button'
import { Link } from 'react-router-dom'

export function MainHeader({ toggleDarkMode }) {
  const onToggleDarkMode = () => {
    toggleDarkMode()
  }

  return (
    <header className='app-header'>
      <section className='container'>
        <h1 className='logo'>Mr. Bitcoin</h1>
        <nav>
          <Link to='/'>
            <Button variant='outlined'>Home</Button>
          </Link>
          <Link to='/contact'>
            <Button variant='outlined'> Contacts</Button>
          </Link>
          <Link to='/statistic'>
            <Button variant='outlined'>Statistics</Button>
          </Link>
          <Button onClick={onToggleDarkMode} variant='outlined'>
            Dark Mode
          </Button>
        </nav>
      </section>
    </header>
  )
}
