const axios = require('axios')

export const bitcoinService = {
  getRate,
  getMarketPrice,
}

async function getRate(coins) {
  const res = await axios.get(
    ` https://blockchain.info/tobtc?currency=USD&value=${coins}`
  )
  return res.data
}

